.App {
  text-align: left; }

.App-logo {
  /*animation: App-logo-spin infinite 20s linear;*/
  height: 80px; }

.App-red-logo {
  float: right; }

.App-header {
  background-color: #222;
  height: 230px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  text-align: center;
  padding-top: 20px; }

.App-links {
  text-decoration: none;
  color: white;
  padding-right: 30px;
  font-size: 20px; }

.home-parent {
  text-align: center;
  padding: 0px 20px; }

li {
  margin: 0 auto;
  text-align: center;
  padding: 20px;
  border: 2px solid grey;
  display: inline-block;
  width: 180px; }

li:nth-child(odd) {
  background: yellow; }

li:nth-child(even) {
  background: lightgreen; }

.showcase-parent {
  text-align: center; }

video {
  padding: 10px; }

.particles-js {
  position: absolute;
  z-index: -10;
  width: 100%;
  height: 100%;
  background: transparent; }

circle {
  background: #f00;
  width: 140px;
  height: 140px;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  line-height: 140px;
  margin: 10px;
  font-weight: bold; }

.features-container {
  text-align: center; }



